<template>
	<div id="cart">
		<div class="cart-box" v-for="(item,index) in cart_list.goods_data">
			<ul class="cart-lists">
				<li v-bind:style="{width:'10%'}">
					<input type="checkbox" v-bind:value="index" v-model="checklist">
				</li>
				<li v-bind:style="{width:'20%'}">
					<el-image v-bind:style="{'width':'5rem','height':'5rem','border':'1px solid white'}" :src="require('@/'+item.goodsSrc)" lazy></el-image>
				</li>
				<li v-bind:style="{width:'35%'}">
					<ul class="cart-goodinfo">
						<li><span>{{item.goodsName}}</span>&nbsp;<span>{{item.goodsId}}</span></li>
						<li><span>规格</span>&nbsp;<span>中等</span></li>
						<li><span v-bind:style="{'color':'#ff5500'}"><strong>${{item.newPrice}}</strong></span>&nbsp;<span v-bind:style="{'text-decoration':'line-through'}">${{item.oldPrice}}</span></li>
					</ul>
				</li>
				<li v-bind:style="{width:'35%'}">
					<span>
						<a href="javascript:0;" v-on:click="add_nums(index)"><i class="el-icon-plus"></i></a>
						<a href="javascript:0;">{{item.nums}}</a>
						<a href="javascript:0;" v-on:click="reduce_nums(index)"><i class="el-icon-minus"></i></a>
						<a href="javascript:0;" v-on:click="remove_goods(index)"><i class="el-icon-delete"></i></a>
					</span>
				</li>
			</ul>
		</div>
		<div class="footer_cart">
			<ul>
				<li>
					<router-link to="/">返回首页</router-link>
				</li>
				<li><a href="javascript:0;">共计:{{totalprice}}元</a></li>
				<li><a href="javascript:0;" v-on:click="gopay()">立即购买</a></li>
			</ul>
		</div>
		<el-button v-bind:style="{'display':'none'}" :plain="true" @click="gopay()"></el-button>
	</div>
</template>

<script>
	export default {
		name: 'goodslist',
		data: function() {
			return {
				checklist: [],
				cart_list:{
					goods_data:[]
				}
			}
		},
		methods:{
			add_nums:function(i){
				if(this.cart_list.goods_data[i].nums<100){
					this.checklist=[];
					this.$store.dispatch('commit_addgoodsnums_to_carlist',{index:i});
					this.$forceUpdate();
				}
			},
			reduce_nums:function(i){
				if(this.cart_list.goods_data[i].nums>1){
					this.checklist=[];
					this.$store.dispatch('commit_reducegoodsnums_to_carlist',{index:i});
					this.$forceUpdate();
				}
			},
			remove_goods:function(i){
				this.checklist=[];
				this.$store.dispatch('commit_removegoods_from_cartlist',{index:i});
			},
			gopay:function(){
				this.$message({
					message: '抱歉暂时还不能购买!',
					type: 'warning'
				});
			}
		},
		computed:{
			totalprice:function(){
				var total=0;
				for(var x in this.checklist){
					total += this.cart_list.goods_data[x].newPrice*this.cart_list.goods_data[x].nums;
				}
				return total;
			}
		},
		mounted: function() {
			this.cart_list.goods_data=this.$store.getters.get_cart_list;
		}
	}
</script>

<style lang="css" scoped="scoped">
	#cart{
		margin-bottom: 3.33333rem;
	}
	.cart-box {
		width: 100%;
		height: 5.5rem;
		padding: 0.2rem;
		border: 1px solid white;
	}

	.cart-lists {
		width: 100%;
		height: 5rem;
		margin: 0;
		padding: 0;
		display: flex;
		flex-direction: row;
		justify-content: left;
		border-radius: 0.2rem;
		background-color: #92cfa0;
	}

	.cart-lists>li {
		height: 5rem;
		line-height: 5rem;
		list-style: none;
	}
	.cart-lists>li:nth-child(3){
		line-height: 0;
	}
	.cart-goodinfo{
		width: 100%;
		height: 5rem;
		margin: 0;
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}
	.cart-goodinfo>li{
		list-style: none;
		margin:0 auto;
	}
	.cart-lists>li:nth-child(4){
		line-height: 8rem;
	}
	.cart-lists>li:nth-child(4)>span>a{
		width: 1.5rem;
		height: 1.5rem;
		text-decoration: none;
		border-radius: 0.2rem;
		border: 1px solid white;
	}
	.footer_cart {
		position: fixed;
		z-index: 1000;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 3.33333rem;
	}

	.footer_cart>ul {
		width: 100%;
		padding: 0;
		margin: 0;
	}

	.footer_cart>ul>li {
		width: 33%;
		line-height: 3.33333rem;
		list-style: none;
		border-radius: 2px;
		background-color: #38c396;
		border: 0.05px solid #beb9bb;
		display: inline-block;
	}

	.footer_cart>ul>li:nth-child(3) {
		background-color: #1e948a;
	}

	.footer_cart>ul>li>a {
		width: 100%;
		font-size: 1.2rem;
		line-height: 3.33333rem;
		text-decoration: none;
		color: white;
	}
</style>
